*,
*::before,
*::after {
    box-sizing: border-box;
}

.container {
    width: 100%;
    max-width: var(--container-width);
    padding: 0 30px;
    margin: 0 auto;
}

html,
body {
    height: 100%;
    margin: 0;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: auto;
    color: var(--text-color);
}

img {
    height: auto;
    max-width: 100%;
    width: 100%;
    object-fit: cover;
}

.img-wrap {
    width: 100%;
    height: auto;
}

a {
    text-decoration: none;
    color: inherit;
}

.btn-reset {
    border: none;
    padding: 0;
    background-color: transparent;
    cursor: pointer;
}

.list-reset {
    list-style: none;
    margin: 0;
    padding: 0;
}

.hyphenated {
    hyphens: auto;
    word-break: break-word;
}

.h-reset {
    margin: 0;
    font-size: inherit;
}

.flex-row {
    display: flex;
    align-items: center;
}

.embed-container {
    position: relative;
    padding-bottom: 56.25%;
    height: 0;
    overflow: hidden;
    max-width: 100%;
    z-index: 1;
}

.embed-container iframe,
.embed-container object,
.embed-container embed {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.flex-column {
    display: flex;
    flex-direction: column;
}

.grid-row {
    display: grid;
}

.accent_button {
    display: block;
    max-width: max-content;
    border-radius: 300px;
    padding: 16px 24px;
    background: var(--accent-color);
    font-weight: 700;
    font-size: 16px;
    line-height: 120%;
    color: #fff;
}

.accent_button.project_link {
    padding: 32px 48px;
    display: flex;
    align-items: center;
    gap: 16px;
    font-size: 20px;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    z-index: 2;
}

