.section {
    margin: 120px 0;
}

.project_title_h1 {
    margin: 0;
    font-family: var(--second-family);
    font-weight: 600;
    font-size: 76px;
    line-height: 100%;
    letter-spacing: -0.03em;
    text-align: center;
    text-wrap: balance;
}

.project_title {
    margin: 0;
    font-family: var(--second-family);
    font-weight: 600;
    font-size: 48px;
    line-height: 100%;
    letter-spacing: -0.03em;
    text-align: center;
}

.project_description {
    line-height: 140%;
    text-align: center;
    margin: 0;
    text-wrap: balance;
}

.mb40 {
    margin-bottom: 40px;
}

.mb60 {
    margin-bottom: 60px;
}

.mt60 {
    margin-top: 60px;
}